.slider__icon:after, .slider__icon:before {
  content: '';
  display: inline-block;
  position: relative;
  top: -8px;
}

.slider__icon:before {
  background-image: url(../images/sprite.png);
  background-position: 0px 0px;
  width: 28px;
  height: 8px;
  margin-right: 24px;
}

.slider__icon:after {
  background-image: url(../images/sprite.png);
  background-position: 0px -8px;
  width: 28px;
  height: 8px;
  margin-left: 24px;
}

.slider__title {
  font-size: 18px;
  line-height: 1.44444;
  color: #fff;
  text-transform: uppercase;
  font-family: 'Dosis', sans-serif;
  text-align: center;
  margin: 47px 0;
}

.slider__quote .main__description, .slider__quote .discography__text, .slider__quote .concert__text, .slider__quote .lattercon__text, .slider__quote .slider__quote-end {
  margin-bottom: 73px;
}

.slider__quote-end {
  display: block;
  margin-bottom: 0;
}
