@import "src/_head";

@mixin sans {
	font-family: 'Open Sans', sans-serif; }

@mixin sansBold {
	font-family: 'Open Sans', sans-serif;
	font-weight: 700; }

@mixin dosis {
	font-family: 'Dosis', sans-serif; }

$t: .2s linear;

.topline {
	background: rgba($b, .1);
	@include pf;
	left: 0;
	right: 0;
	top: 0;
	z-index: 10;
	transition: background $t;
	&.--dark {
		background: rgba($b, .7);
		transition: background $t;
		box-shadow: 0px -1px 15px 1px rgba($b, .5);
		.topline__menu {
			a {
				color: $w;
				&:hover {
					color: $w;
					&:before {
						background: $w; }
					@include r(992) {
						color: #727272; } } } }
		.menu__toggle {
			span {
				background: $w; } } }
	&__menu {
		margin: 0;
		padding: 0;
		@include df;
		justify-content: space-between;
		@include fw;
		@include rmin(992) {
			display: flex !important; }
		@include r(992) {
			display: none;
			flex-direction: column;
			height: 100%;
			max-height: calc(100vh - 100px);
			overflow-y: auto;
			padding: 10px 0;
			li {
				display: block;
				a {
					transition: background $t, padding $t;
					&:hover {
						padding: 0 10px;
						background: $w;
						transition: background $t, padding $t; }
					&:before {
						display: none; } } } }
		li {
			display: inline-block;
			a {
				display: block;
				@include dosis;
				text-transform: uppercase;
				font-size: 17px;
				@include lh(59, 17);
				@include pr;
				color: #727272;
				&:before {
					opacity: 0;
					@include c;
					@include pa;
					left: 0;
					right: 0;
					bottom: 10px;
					height: 2px;
					background: $b;
					transition: opacity $t, color $t; }
				&:hover {
					color: #5f5f5f;
					&:before {
						opacity: 1;
						transition: opacity $t, color $t; } } } } } }

.menu__toggle {
	display: none;
	width: 50px;
	height: 50px;
	float: right;
	border: 0;
	background-color: transparent;
	@include pr;
	span {
		@include pa;
		display: block;
		width: 70%;
		left: 50%;
		margin-left: -18px;
		height: 4px;
		background: $b;
		border-radius: 2px;
		&:nth-of-type(1) {
			top: 12px;
			transition: top $t, transform $t; }
		&:nth-of-type(2) {
			top: 50%;
			margin-top: -2px;
			transition: opacity $t; }
		&:nth-of-type(3) {
			bottom: 12px;
			transition: bottom $t, transform $t; } }
	@include r(992) {
		display: block; }
	&.--active {
		span {
			&:nth-of-type(1) {
				top: 23px;
				transform: rotate(45deg);
				transition: top $t, transform $t; }
			&:nth-of-type(2) {
				opacity: 0;
				transition: opacity $t; }
			&:nth-of-type(3) {
				bottom: 23px;
				transform: rotate(-45deg);
				transition: bottom $t, transform $t; } } } }

.header {
	min-height: 1062px;
	height: 1062px;
	max-width: 2000px;
	margin: 0 auto;
	@include df;
	@include cc;
	background-size: cover;
	background-position: 50% 0;
	@include r(450) {
		background-position: 45% 0; }
	&__text {
		background-image: url('../images/hero-text.png');
		min-width: 450px;
		min-height: 418px;
		height: 418px;
		background-size: cover;
		background-position: 50% 0;
		// transform: translateY(-110px)
		@include r(450) {
			width: 50%;
			min-width: 100%;
			background-size: 100%;
			background-repeat: no-repeat; } } }

.main__title {
	text-align: center;
	text-transform: uppercase;
	@include dosis;
	font-size: 50px;
	@include lh(50, 59);
	margin: 0;
	color: $b;
	background: transparent;
	background: -webkit-linear-gradient(transparent, transparent), url('../images/text-pattern.png') repeat;
	background-position: 0 58%;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	@include pr;
	&.black {
		background-image: none;
		-webkit-text-fill-color: #000;
		color: $b; }
	&.red {
		background-position: 0 80%; }
	&.blue {
		background: -webkit-linear-gradient(#62a2ad, #8dbab3);
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent; }
	&:before {
		@include c;
		@include pa;
		left: 0;
		right: 0;
		height: 1px;
		background: $b;
		top: 50%;
		z-index: -1;
		@include r(320) {
			display: none; } } }

.main__description {
	padding: 0;
	text-align: center;
	@include sans;
	color: #798285;
	font-size: 14px;
	@include lh(24, 14);
	margin-bottom: 155px; }

section {
	padding: 160px 0 65px; }

.discography {
	padding-bottom: 400px;
	background-image: url('../images/discography.png');
	background-repeat: no-repeat;
	background-position: 50% 100%;
	background-size: contain;
	max-width: 4000px;
	margin: 0 auto;
	@include rmin(2000) {
		min-height: 2000px;
		background-size: 100% !important; }
	@include r(992) {
		padding-bottom: 200px; }
	@include r(480) {
		padding-bottom: 100px; }
	&__item {
		margin-bottom: 113px;
		@include clr;
		&:last-child {
			.button {
				margin-top: 100px !important; } }
		@include r(769) {
			margin-bottom: 50px; } }
	&__category {
		.year {
			@include sansBold;
			color: #5c5c5c;
			&:after {
				@include c;
				@include pr;
				display: inline-block;
				width: 100%;
				height: 1px;
				background: $b;
				top: -5px; } }
		.category {
			a {
				@include sansBold;
				color: $b;
				text-transform: uppercase; }
			@include r(769) {
				margin-bottom: 20px; } } }
	&__title {
		a {
			font-size: 23px;
			color: $b;
			@include lh(24, 23); } }
	&__text {
		@extend .main__description;
		text-align: left;
		margin-bottom: 24px;
		.button {
			@include r(769) {
				margin: 0 auto; } } } }

.button {
	min-height: 56px;
	height: 56px;
	max-width: 196px;
	@include df;
	@include cc;
	color: $b;
	@include sansBold;
	@include pr;
	&:hover {
		color: $b;
		&:before {
			background: rgba($w, .1);
			transition: background $t; } }
	&:before {
		@include pa;
		@include c;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		background: none;
		transition: background $t;
		z-index: 1; }
	&.white {
		color: $w;
		&:hover {
			color: $w; } }
	&.stone {
		background-image: url('../images/stone.png');
		background-size: cover; }
	&.palm {
		background-image: url('../images/palm.png');
		background-size: cover; }
	&.foliage {
		background-image: url('../images/foliage.png');
		background-size: cover; }
	&.with__border {
		border: 1px solid #b3b5b5; }
	&.svg {
		transition: background $t;
		svg {
			width: 5px;
			height: 10px;
			left: 15px;
			transition: left $t;
			@include pr; }
		&:hover {
			background: rgba($w, .2);
			transition: background $t;
			svg {
				left: 18px;
				transition: left $t; } } } }

.concert {
	min-height: 1250px;
	background-image: url('../images/concert.png');
	background-repeat: no-repeat;
	background-position: 50% 100%;
	background-size: contain !important;
	max-width: 4000px;
	margin: 0 auto;
	@include rmin(2000) {
		min-height: 2000px;
		background-size: 100% !important; }
	&__item {
		@include clr;
		margin-top: 170px; }
	&__category {
		@extend .discography__category;
		margin-bottom: 85px;
		.year {
			&:after {
				display: block;
				margin-top: 10px;
				width: 50%;
				@include r(769) {
					width: 100%; } } } }
	&__title {
		@extend .discography__title; }
	&__text {
		@extend .discography__text;
		@include r(992) {
			p {
				padding: 15px;
				background: rgba($w, .6); } }
		@include r(769) {
			p {
				padding: 0; } } } }

.lattercon {
	&__item {
		@include df;
		justify-content: space-between;
		margin-bottom: 54px;
		@include r(992) {
			display: block; }
		.lattercon__info {
			padding: 15px; }
		.lattercon__info,
		.lattercon__video {
			width: 50%;
			@include r(992) {
				width: 100%; } }
		&.info__left,
		&.info__right, {
			.lattercon__info {
				&:before {
					@include c;
					@include pa;
					z-index: 1;
					top: 50%;
					margin-top: -10px;
					@include r(992) {
						@include arr(20, 13, #f5f6f6, b);
						top: auto;
						bottom: -13px;
						left: 50%;
						margin-left: -10px; } } }
			.lattercon__video {
				background: $b;
				color: $w; } }
		&.info__left {
			.lattercon__info {
				order: 1;
				&:before {
					right: -13px;
					@include arr(13, 20, #f5f6f6, r); } }
			.lattercon__video {
				order: 2; } }
		&.info__right {
			.lattercon__info {
				order: 2;
				&:before {
					left: -13px;
					@include arr(13, 20, #f5f6f6, l); } }
			.lattercon__video {
				order: 1; } } }
	&__info {
		padding: 35px;
		background: #f5f6f6;
		@include pr;
		min-height: 500px;
		@include r(992) {
			padding: 0;
			min-height: auto; } }
	&__category {
		@extend .discography__category;
		margin-bottom: 50px;
		.year {
			&:after {
				display: block;
				margin-top: 10px;
				width: 75%;
				@include r(769) {
					width: 100%; } } } }
	&__title {
		@extend .discography__title; }
	&__text {
		@extend .discography__text; }
	&__sub {
		@include df;
		justify-content: center;
		@include r(1262) {
			display: block; }
		&-left {
			width: 40%; }
		&-right {
			width: 60%;
			@include r(1262) {
				margin-top: 20px; } }
		@include r(1262) {
			&-left,
			&-right, {
				width: 100%; } } } }
.sub {
	font-size: 12px;
	color: #abacac;
	@include clr;
	margin: 0;
	padding: 0;
	height: 100%;
	@include df;
	align-items: center;
	@include r(1262) {
		display: block; }
	svg {
		width: 15px;
		height: 15px;
		display: inline-block;
		@include pr;
		top: 3px;
		margin-right: 10px; }
	li {
		float: left;
		margin-left: 20px;
		&:first-child {
			margin-left: 0; } }
	a {
		@extend .sub;
		display: inline-block !important; } }

.slider {
	background-image: url('../images/slider.png');
	background-size: cover;
	background-position: 50% 0;
	padding: 180px 0;
	@include pr;
	&__wrap {
		overflow-x: hidden; }
	.item {
		color: $w !important;
		overflow: hidden; }
	&__next,
	&__prev, {
		// display: block !important
		cursor: pointer;
		@include pa;
		top: 50%;
		margin-top: -85px;
		@include r(769) {
			display: none !important; }
		svg {
			width: 12px;
			height: 23px;
			@include pa;
			top: -10px; } }
	&__next {
		right: 0;
		@include arr(85, 170, $w, l);
		@include r(992) {
			margin-top: -40px;
			@include arr(40, 70, $w, l); }
		svg {
			right: -60px;
			@include r(992) {
				right: -32px; } } }
	&__prev {
		left: 0;
		@include arr(85, 170, $w, r);
		@include r(992) {
			margin-top: -40px;
			@include arr(40, 70, $w, r); }
		svg {
			left: -60px;
			transform: rotate(-180deg);
			@include r(992) {
				left: -32px; } } }
	&__icon {
		text-align: center;
		font-size: 0;
		svg {
			fill: $w;
			width: 30px;
			height: 30px; }
		&:after,
		&:before {
			@include c;
			display: inline-block;
			@include pr;
			top: -8px; }
		&:before {
			@include sprite($slider-top-left);
			margin-right: 24px; }
		&:after {
			@include sprite($slider-top-right);
			margin-left: 24px; } }
	&__title {
		font-size: 18px;
		@include lh(26, 18);
		color: $w;
		text-transform: uppercase;
		@include dosis;
		text-align: center;
		margin: 47px 0; }
	&__quote {
		.main__description {
			margin-bottom: 73px; }
		&-end {
			@extend .main__description;
			display: block;
			margin-bottom: 0; } } }
