// font-family без формата woff2
@mixin font($alias, $name) {
	@font-face {
		font-family: $alias;
		src: url("../fonts/" + $name + "/" + $name + ".eot");
		src: url("../fonts/" + $name + "/" + $name + ".eot?#iefix") format("embedded-opentype"), url("../fonts/" + $name + "/" + $name + ".woff") format("woff"), url("../fonts/" + $name + "/" + $name + ".ttf") format("truetype");
		font-weight: normal;
		font-style: normal; } }


// font-family с форматом woff2
@mixin fontWoff2($alias, $name) {
	@font-face {
		font-family: $alias;
		src: url("../fonts/" + $name + "/" + $name + ".eot");
		src: url("../fonts/" + $name + "/" + $name + ".eot?#iefix") format("embedded-opentype"), url("../fonts/" + $name + "/" + $name + ".woff2") format("woff2"), url("../fonts/" + $name + "/" + $name + ".woff") format("woff"), url("../fonts/" + $name + "/" + $name + ".ttf") format("truetype");
		font-weight: normal;
		font-style: normal; } }


@mixin hide-text {
	font: 0/0 a;
	text-shadow: none;
	color: transparent; }

@mixin hidetext {
	@include hide-text; }

@mixin justify {
	text-align: justify;
	line-height: 0;
	font-size: 0;
	text-justify: newspaper;
	zoom: 1;
	text-align-last: justify;
	&:after {
		content: "";
		display: inline-block;
		width: 100%;
		height: 0px;
		visibility: hidden;
		overflow: hidden; } }

@mixin prevent-text {
	-ms-word-break: break-all;
	word-break: break-all;
	word-break: break-word;
	-webkit-hyphens: auto;
	-moz-hyphens: auto;
	hyphens: auto; }

@mixin font-smoothing($val: antialiased) {
	-webkit-font-smoothing: $val;
	-moz-font-smoothing: $val;
	-ms-font-smoothing: $val;
	-o-font-smoothing: $val; }


//Управляет поведением выделения текста и других элементов на странице.
@mixin unselectable {
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none; }

@mixin nosel {
	@include unselectable; }

//применяет стиль к выделенному пользователем тексту.
@mixin selection {
	& ::selection {
		@content; }
	& ::-moz-selection {
		@content; } }

@mixin sel {
	@include selection; }

@function makelongshadow($color) {
	$val: 0px 0px $color;
	@for $i from 1 through 200 {
		$val: #{$val}, #{$i}px #{$i}px #{$color}; }
	@return $val; }

@mixin longshadow($color) {
	text-shadow: makelongshadow($color); }

@mixin ts($string) {
	-webkit-text-shadow: $string;
	-moz-text-shadow: $string;
	-ms-text-shadow: $string;
	-o-text-shadow: $string;
	text-shadow: $string; }

@mixin links($link, $visit, $hover, $active) {
	a {
		color: $link;
		&:visited {
			color: $visit; }
		&:hover {
			color: $hover; }
		&:active {
			color: $active; } } }

