@import "src/_base";
@import "src/_fonts";
@import "src/_func";
@import "helpers/generated/_sprites";
@import "lib/slick-theme";
@import "../libs/slick-carousel/slick/slick.scss";
$output-bourbon-deprecation-warnings: false;
@import "bourbon";

@import url('https://fonts.googleapis.com/css?family=Dosis|Open+Sans:400,700');

$font: "Helvetica Neue",Arial,sans-serif;
$sans: 'Open Sans', sans-serif;
$dosis: 'Dosis', sans-serif;


ol, ul {
	list-style: none; }

blockquote, q {
	quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none; }

table {
	border-collapse: collapse;
	border-spacing: 0; }

// main
html, body {
	height: 100%; }

body {
	height: 100%;
	margin: 0;
	padding: 0;
	background: $bg;
	font-family: $sans; }

@import "_preloader";
